<template>
	<div id="merchant-storage">
		<public-header title="商家回收站"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-notab">
				<van-search class="padding-10" placeholder="请输入店铺ID或店铺名称" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="storage-list"
			>
				<div class="storage-item" v-for="(item, index) in records.data" :key="item.id">
					<van-row>
						<van-col span="6">
							<div class="storage-img">
								<img :src="item.logo" alt="">
							</div>
						</van-col>
						<van-col span="18">
							<div class="storage-right">
								<div class="store-name">{{item.title}}</div>
								<div class="storage-info flex">
									<div>联系电话：</div>
									<div>{{item.telephone}}</div>
								</div>
								<div class="storage-info flex">
									<div>商户地址：</div>
									<div>{{item.address}}</div>
								</div>
								<div class="storage-info flex">
									<div>销毁时间：</div>
									<div>{{item.deltime_cn}}</div>
								</div>
							</div>
						</van-col>
					</van-row>
					<div class="border-1px-t btn-group">
						<div class="btn-item bg-default" @click="onDeleteStore(item, index)">彻底删除</div>
						<div class="btn-item bg-primary" @click="onRecoverStore(item, index)">恢复</div>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/merchant/storage/list'
				});
			},
			onDeleteStore(item, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/merchant/storage/del',
					data: {
						id: item.id,
					},
					confirm: '确定删除商户吗？',
					success:function(){
						that.records.data.splice(index, 1);
					}
				});
			},
			onRecoverStore(item, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/merchant/storage/restore',
					data: {
						id: item.id,
					},
					confirm: '确定恢复商户吗？',
					success:function(){
						that.records.data.splice(index, 1);
					}
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#merchant-storage .content .storage-item{
		background-color: #fff;
		margin-bottom: 10px;
	}
	#merchant-storage .content .storage-item .storage-img{
		width: 90px;
		height: 100px;
		padding: 15px 10px;
	}
	#merchant-storage .content .storage-item .storage-img img{
		width: 100%;
		height: 100%;
		border-radius: 5px;
	}
	#merchant-storage .content .storage-item .storage-right{
		padding: 10px 10px 10px 0;
	}
	#merchant-storage .content .storage-item .storage-right .store-name{
		font-size: 16px;
		font-weight: bold;
		height: 32px;
		line-height: 32px;
		padding-bottom: 8px;
	}
	#merchant-storage .content .storage-item .storage-right .storage-info{
		font-size: 12px;
		color: #7a7a7a;
		padding-bottom: 5px;
	}
	#merchant-storage .content .storage-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#merchant-storage .content .storage-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


